export default ".btn-toolbar {\n  justify-content: center; }\n\n/* GLOBAL STYLES\n-------------------------------------------------- */\n\n/* Padding below the footer and lighter body text */\n\nbody {\n  padding-top: 3rem;\n  padding-bottom: 3rem;\n  color: #5a5a5a; }\n\nh2 {\n  color: white; }\n\nmain {\n  background-color: #343a40;\n  height: 100%; }\n\n/* CUSTOMIZE THE CAROUSEL\n-------------------------------------------------- */\n\n/* Carousel base class */\n\n.carousel {\n  margin-bottom: 4rem; }\n\n/* Since positioning the image, we need to help out the caption */\n\n.carousel-caption {\n  bottom: 3rem;\n  z-index: 10; }\n\n/* Declare heights because of positioning of img element */\n\n.carousel-item {\n  height: 32rem; }\n\n.carousel-item > img {\n  position: absolute;\n  top: 0;\n  left: 0;\n  min-width: 100%;\n  height: 32rem; }\n\n/* MARKETING CONTENT\n-------------------------------------------------- */\n\n/* Center align the text within the three columns below the carousel */\n\n.marketing .col-lg-4 {\n  margin-bottom: 1.5rem;\n  text-align: center; }\n\n.marketing h2 {\n  font-weight: 400; }\n\n.marketing .col-lg-4 p {\n  margin-right: .75rem;\n  margin-left: .75rem; }\n\n/* Featurettes\n------------------------- */\n\n.featurette-divider {\n  margin: 5rem 0;\n  /* Space out the Bootstrap <hr> more */ }\n\n/* Thin out the marketing headings */\n\n.featurette-heading {\n  font-weight: 300;\n  line-height: 1;\n  letter-spacing: -.05rem; }\n\n/* RESPONSIVE CSS\n-------------------------------------------------- */\n\n@media (min-width: 40em) {\n  /* Bump up size of carousel content */\n  .carousel-caption p {\n    margin-bottom: 1.25rem;\n    font-size: 1.25rem;\n    line-height: 1.4; }\n  .featurette-heading {\n    font-size: 50px; } }\n\n@media (min-width: 62em) {\n  .featurette-heading {\n    margin-top: 7rem; } }\n\n/*# sourceMappingURL=data:application/json;base64, */"